.coca-upload-media__component {
	display: block;
	width: 100%;

	.coca-upload-media {
		position: relative;
		display: block;
		width: 150px !important;
		height: 150px !important;
		overflow: hidden;
		cursor: pointer;
		border: 1px dashed #000;
		border-radius: 4px;

		&:has(svg.placeholder-broken-image) {
			border: 1px dashed #f00;
		}

		&:has(svg.placeholder-broken-image) .footer {
			background-color: #f00;
		}

		.remove-icon {
			position: absolute;
			top: 10px;
			right: 10px;
			z-index: 5;
			height: auto;
			padding: 2px;
			line-height: 0;
			color: #fff;
			background-color: #f00;
			border-radius: 4px;
			transition: 300ms ease-in-out;

			svg {
				width: 16px;
				height: auto;
				fill: currentcolor;
			}
		}

		img {
			position: relative;
			max-width: 100%;
			height: 100%;
			line-height: 0;
		}

		svg.placeholder-broken-image {
			fill: #f00;
		}

		svg.placeholder-upload-image {
			fill: #000;
		}

		.overlay {
			position: absolute;
			right: 0;
			bottom: 0;
			left: 0;
			display: block;
			width: 100%;
			height: 40px;
			text-align: center;

			.footer {
				box-sizing: border-box;
				display: inline-flex;
				align-items: center;
				justify-content: center;
				width: 100%;
				height: 100%;
				padding: 0;
				margin: 0;
				font-family: inherit;
				font-size: 13px;
				font-weight: 400;
				color: #fff;
				text-decoration: none;
				white-space: nowrap;
				appearance: none;
				cursor: pointer;
				background-color: rgb(0 0 0 / 50%);
				border: 0;
				border-radius: unset;
				outline: 0;
				box-shadow: none;

				// transform: translateY(100%);
				transition: 300ms ease-in-out;

				&:hover,
				&:active {
					background-color: rgb(0 0 0 / 80%) !important;
				}
			}

		}

		&:hover .overlay button {
			// transform: translateY(0);
		}
	}

	.components-notice.is-error {
		padding: 0 0 0 10px;
		margin-top: 1rem;
		margin-right: 0;
		margin-left: 0;
	}
}
