.coca-disable-feature {
	position: relative;
	display: block;
	width: 100%;
	padding-right: 20px;
	padding-left: 20px;
	margin-right: -20px;
	margin-left: -20px;
	overflow: hidden;

	.components-disabled {
		cursor: not-allowed;
		opacity: .5;
	}

	.buy-pro-overlay {
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		z-index: 10;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;
		background-color: rgb(255 255 255 / 57%);
		border: 0;
		transition: 300ms ease-in-out;
		transform: translateY(100%);
	}

	&:hover .buy-pro-overlay {
		transform: translateY(0);
	}
}
